import React from "react";
import Styled from "styled-components";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import { faPlay, faPause } from "@fortawesome/free-solid-svg-icons";



const Button = Styled.button`
width:50px;
height:50px;
background-color:#16b8f3;
outline: none;
border: 1px solid #16b8f3;
border-radius:50%;
box-shadow: 7px 7px 15px rgba(0,0,0,0.3), 
           -7px -7px 15px  rgba(255,255,255,0.3);
&:active
{
    box-shadow: 7px 7px 15px rgba(0,0,0,0.3), 
              -7px -7px 15px  rgba(255,255,255,0.3),
              7px 7px 15px rgba(0,0,0,0.3) inset, 
            -7px -7px 15px  rgba(255,255,255,0.3) inset;
}

`;

const Input = Styled.input`
width:70px;
position:relative;
height:70px;
background-color:#16b8f3;
appearance:none;
outline: none;
border: 1px solid #16b8f3;
border-radius:50%;
box-shadow: 7px 7px 15px rgba(0,0,0,0.3), 
           -7px -7px 15px  rgba(255,255,255,0.3);


&:checked
{
    box-shadow: 7px 7px 15px rgba(0,0,0,0.3), 
              -7px -7px 15px  rgba(255,255,255,0.3),
              7px 7px 15px rgba(0,0,0,0.3) inset, 
            -7px -7px 15px  rgba(255,255,255,0.3) inset;
}
`;

const UIcomponent = (props) => {
  switch(props.type){
      case('Button'):{
      return <Button style = {{color:'white'}} onClick = {props.onClick}>{props.children}</Button>
      }

      case('Check'):{
        return <div style={{color:'white'}}>
          <Input type="checkbox" onChange={props.clicked} checked={props.play} ></Input>
          <FontAwesomeIcon size='2x' style={{ transform:'translate(-51px, -24px)'}} onClick={props.clicked} icon={props.play?faPause:faPlay}></FontAwesomeIcon>
        </div>

    }
      default:{
          return null
      }
  }
};

export default UIcomponent;
