import React from "react";
import Styled from "styled-components";
import item1 from "../../Assests/Images/Airplanes.jpg";
import item2 from "../../Assests/Images/Dooriyan.jpg";
import item3 from "../../Assests/Images/Metallica.jpg";

const Div = Styled.div`
  width:30%;
  height:75%;
  border-radius:3px;
  margin-left: 2px;
  margin-right:2px;
  margin-top:8px;
  overflow: hidden;
  display:inline-block;
  position: relative;
  box-sizing: border-box;
  &::before{  
      content:"";
      background: url("${(props) => props.imgUrl}") no-repeat center/cover;
      position: absolute;
      width:100%;
      left:0px;
      height:100%;
      transition: all 0.9s ease;
  }
  &::after{  
    content:${(props)=>{return props.nowPlaying? '\'Now Playing\';': `'${props.description}';`}}
    padding-top:${(props)=>{return props.nowPlaying? '30px;': '0px;' }}
    position: absolute;
    background-color: rgba(253,165,15, 0.9);
    left:0px;
    width:100%;
    height:100%;
    transform: ${(props)=>{return props.nowPlaying? 'transform:translateY(0);': 'translateY(-100%);' }}
    transition: all 0.5s ease;
}
  &:hover::before{
      transform:scale(1.2);
  }
  &:hover::after{
    transform:translateY(0); 
}

  `;

const arr = [
  {
    imgUrl: item1,
    link: "",
    description:"Airplanes by BoB"},
  {
    imgUrl: item2,
    link: "",
    description:"Dooriyaan in movie Love Aj Kal"
  },
  {
    imgUrl: item3,
    link: "",
    description:"Nothing Else Matter by Mettalica" 
  }
];

const Cards = (props) => {
  return arr.map((project, id) => (
    <Div key={id} imgUrl={project.imgUrl} onClick = {props.clicked.bind(null,id)} description = {project.description} nowPlaying = {(props.id-1)===id} >
    </Div>
  ));
};

export default Cards;
