import React, { useState, useRef, useEffect } from "react";
import cssClasses from "./Wrapper.module.scss";
import Screen from "../Components/Screen/Screen";
import UIcomponent from "../Components/UI-Components/UIComponent";
// import Slider from "rc-slider";
// import "rc-slider/assets/index.css";
import PlayList from "../Components/Playlist/Playlist";
import Song from "../Assests/Airplanes.mp3";
import Song2 from "../Assests/Dooriyan.mp3";
import Song3 from "../Assests/Metallica.mp3";
import {
  faAngleDoubleLeft,
  faAngleDoubleRight,
  faVolumeUp,
  faVolumeMute,
  faVolumeDown,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { airplanes,metallica,dooriyan } from "../Lyrics/Lyrics";
import Slider from "react-input-slider";


// import { Slider } from "react-slider-measure";

const SongArray = [{songId:1,songName:Song,Lyrics:airplanes,Artist:'B.o.B'},{songId:2,songName:Song2,Lyrics:dooriyan,Artist:''},{songId:3,songName:Song3,Lyrics:metallica,Artist:''}];
const audio = new Audio();


String.prototype.toHHMMSS = function () {
  var sec_num = parseInt(this, 10); // don't forget the second param
  var hours   = Math.floor(sec_num / 3600);
  var minutes = Math.floor((sec_num - (hours * 3600)) / 60);
  var seconds = sec_num - (hours * 3600) - (minutes * 60);

  if (hours   < 10) {hours   = "0"+hours;}
  if (minutes < 10) {minutes = "0"+minutes;}
  if (seconds < 10) {seconds = "0"+seconds;}
  return hours+':'+minutes+':'+seconds;
}


const Wrapper = (props) => {
  const [song,setSong] = useState(SongArray[0])
  const [play, setPlay] = useState(false);
  const [volume,setVolume] = useState(0);
  const [track, setTrack] = useState(0);
  const [muted,setMuted] = useState(false);

  const ref = useRef(null);

  useEffect(()=>{
    console.log(song);
    audio.src=song.songName;
    setTrack(0);
  },[song])

  audio.ontimeupdate = updater;
  audio.volume = volume/100;
  audio.loop=true;
  audio.muted = muted;

  function updater() {
    if (ref.current) {
      if (song.Lyrics[audio.currentTime.toFixed(0).toString()])
        ref.current.innerHTML =
          song.Lyrics[audio.currentTime.toFixed(0).toString()];
    }
  }
 
  function volumenHandler()
  {
    setMuted(prev=>!prev);
  }
  
  
  const playlistHandler =  (id,event)=>
  { 
      setSong(SongArray[id]);
      return "";
  }
  const clickHandler = (event) => {
    setPlay((prevProp) => !prevProp);
  };

  const rightClickHandler = ()=>
  {

      if(song.songId<3)
      {
        setSong(prev=>SongArray[prev.songId]);
      }
      else
      {
        setSong(SongArray[0]);
      }
  }



  const leftClickHandler = ()=>
  {

      if(song.songId>1)
      {
        setSong(prev=>SongArray[prev.songId-2]);
      }
      else
      {
        setSong(SongArray[2]);
      }
  }

  if (audio) {
    let songInterval;
    if (play) {
      audio.play();
      songInterval = setInterval(() => {
        setTrack(audio.currentTime.toFixed(0));
      }, 100);
    } else {
      audio.pause();
      clearInterval(songInterval);
    }
  }

  return (
    <div className={cssClasses.Wrapper}>
      <Screen>
        <p ref={ref} style={{ color: "white" }}></p>
      </Screen>
      <div className={cssClasses.Tracker}>
        <Slider
        axis="x"
        x={track}
        xstep={1}
        xmin={0}
        xmax={audio.duration}
        onChange={(x) => {
          audio.currentTime = x.x
        }}
        
        styles={{
          track: {
            backgroundColor: "#16b8f3",
            height: "20px",
            boxShadow: `7px 7px 15px rgba(0,0,0,0.3), 
          -7px -2px 15px  rgba(255,255,255,0.3),
          7px 7px 15px rgba(0,0,0,0.3) inset, 
          -7px -7px 15px  rgba(255,255,255,0.3) inset;
          `,
            border: "3px solid #16b8f3",
            borderRadius: "15px",
            top: "1px",
            width: "65%",
          },
          active: {
            backgroundColor: "#16b8f3",
            height: "12px",
            boxShadow: `7px 7px 15px rgba(0,0,0,0.3), 
          -7px -2px 15px  rgba(255,255,255,0.3)`,
            left: "1px",
            top: "1px",
          },
          thumb: {
            width: 40,
            height: 25,
            borderRadius:20
          },
        }}
      />
      <p style={{color:'white'}}>{track.toString().toHHMMSS()} / {audio.duration?audio.duration.toFixed(0).toHHMMSS():'0'.toHHMMSS()}</p>
      </div>
      <div className={cssClasses.UI}>
        <UIcomponent type="Button" onClick={leftClickHandler}>
          <FontAwesomeIcon size="2x" icon={faAngleDoubleLeft} ></FontAwesomeIcon>
        </UIcomponent>
        <UIcomponent
          type="Check"
          clicked={clickHandler}
          name="play"
          play={play}
        ></UIcomponent>
        <UIcomponent type="Button" onClick={rightClickHandler}>
          <FontAwesomeIcon
            size="2x"
            icon={faAngleDoubleRight}
          ></FontAwesomeIcon>
        </UIcomponent>
      </div>
      <div className={cssClasses.SliderContainer}>
      <UIcomponent type="Button" onClick={volumenHandler}>
      <FontAwesomeIcon
        className = {cssClasses.Volume}
          size="2x"
          icon={muted?faVolumeMute:volume===0?faVolumeDown:faVolumeUp}
          color="white"
          
        ></FontAwesomeIcon>
        </UIcomponent>

        
        <div className={cssClasses.Slider}>
           <Slider
        axis="x"
       
        xstep={1}
        xmin={0}
        x={volume}
        xmax={100}
        onChange={(x)=>{setVolume(x.x)}}
       
        styles={{
          track: {
            backgroundColor: "#16b8f3",
            height: "20px",
            boxShadow: `7px 7px 15px rgba(0,0,0,0.3), 
          -7px -2px 15px  rgba(255,255,255,0.3),
          7px 7px 15px rgba(0,0,0,0.3) inset, 
          -7px -7px 15px  rgba(255,255,255,0.3) inset;
          `,
            border: "3px solid #16b8f3",
            borderRadius: "15px",
            top: "1px",
            width: "65%",
          },
          active: {
            backgroundColor: "#16b8f3",
            height: "12px",
            boxShadow: `7px 7px 15px rgba(0,0,0,0.3), 
          -7px -2px 15px  rgba(255,255,255,0.3)`,
            left: "1px",
            top: "1px",
          },
          thumb: {
            width: 25,
            height: 25,
          },
        }}
      />
        </div>
      <p style={{color:'white'}}>{volume+'%'}</p>
      </div>
      <PlayList clicked = {playlistHandler} id = {song.songId}></PlayList>
    </div>
  );
};

export default Wrapper;
